<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addCompany">+</el-button>
            </div>     
            <el-form ref="form" :model="form" label-width="180px" class="gap-2">
                <el-row >
                    <el-col :span="12">
                        <el-form-item label="" prop="cityList">
                            <region v-model="form.cityList"></region>
                        </el-form-item>
                        <el-row>
                            <el-col :span="5">
                                <el-form-item label="" prop="corporateStart">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.corporateStart"
                                        type="date"
                                        placeholder=""
                                        style="width:150px"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="19">
                                <el-form-item prop="corporateEnd" label="">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.corporateEnd"
                                        type="date"
                                        placeholder=""
                                        style="width:150px"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="" prop="business">
                            <el-select v-model="form.business" filterable>
                                <el-option label="" value="3"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeAddress">
                            <el-input v-model="form.storeAddress"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="" prop="searchType">
                                    <el-select v-model="form.searchType" filterable>
                                        <el-option label="" value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==1">
                                <el-form-item prop="searchDay" class="expire">
                                    <el-input v-model="form.searchDay" placeholder="" style="width:200px;"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==2">
                                <el-form-item prop="searchDate" class="expire">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        type="date"
                                        placeholder=""
                                        style="width:200px"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        
                        <el-form-item label="" prop="storeName">
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="agent">
                            <el-input v-model="form.agent"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> 95656900</div>
            <el-table :data="searInfoList" border style="width: 100%">
                <el-table-column type="index" label="" align="center" width="100" ></el-table-column>
                <el-table-column prop="city" label="" align="center" ></el-table-column>
                <el-table-column prop="name" label="" align="center" ></el-table-column>
                <el-table-column prop="abbreviation" label="" align="center" ></el-table-column>
                <el-table-column prop="operator" label="" align="center"></el-table-column>
                <el-table-column prop="corporateStart" label="" align="center"></el-table-column>
                <el-table-column prop="corporateEnd" label="" align="center"></el-table-column>
                <el-table-column prop="operator" label="bd" align="center"></el-table-column>
                <el-table-column prop="setUpTime" label="" align="center"></el-table-column>
                <el-table-column prop="corporate" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="editorCompany(scope.$index, scope.row)" type="text">|</el-button>
                        <el-button size="mini" @click="bankAccount(scope.$index, scope.row)" type="text">|</el-button>
                        <el-button size="mini"  type="text" @click="firstDialogVisible = true,handleEnd(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="storeAccount" label="" align="center"></el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pagination.currentPage"
                    :page-sizes="[10, 2, 3, 400]"
                    :page-size="pagination.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pagination.total">
                </el-pagination>
            </div>
            <el-dialog title="" :visible.sync="firstDialogVisible" width="30%" >
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true"></el-button>
                </span>
            </el-dialog>
            <el-dialog title="" :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="noJoin" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="secondDialogVisible = false,continueJoin()"> </el-button>
                    <el-button type="primary" @click="secondDialogVisible = false,endJoin()" > </el-button>
                </span>
            </el-dialog>
            <!---->
            <editor-company v-if="isShow" ref="editor" :currentCompanyInfo="currentCompanyInfo" :title="title" @editSuccess='editSuccess' @addSuccess='addSuccess'></editor-company>
            <!---->
            <commission ref="commission"></commission>
        </el-main>
    </el-container> 
</template>

<script>
import EditorCompany from '@/components/company/_EditorCompany';
import Commission from '@/components/company/_Commission';
import BreadCrumb from '@/components/common/BreadCrumb';
import Region from '@/components/common/Region';

export default {
    name: 'CompanyManagement',
    components:{EditorCompany,Commission,BreadCrumb,Region},
    data () {
        return {
            firstDialogVisible: false,//
            secondDialogVisible:false,//
            noJoin:'',//
            companyInfoIndex:'',//
            currentCompanyInfo:'',//
            title:'',//
            isShow:false,
            // 
            form:{
                abbreviation:'',//
                address:'',//
                agencyId:'',//Id
                businessType:0,//,01.2.3.
                cityId:'',//
                city:'',//
                cityList:[],
                corporate:'',//
                corporatePhone:'',//
                corporateStart:'',//
                corporateEnd:'',//
                deposit:'',//
                name:'',//
                organizationCode:'',//
                operator:'',//
                resourceKey:'',//key
                searchDate:'',//
                searchDay:'',//
                state:'',//1.2. 
                storeAddress:''//
            },
            // 
            pagination:{
                currentPage:1,//1;
                pageSize:10,//10
                total:400//
            },
            breadCrumb: [{text:''},{text: ""}],
            tableData: [{
                city: '',
                name: '',
                address: ' 996 ',
                abbreviation:'',
                corporateStart:'2018-8-8',
                corporateEnd:'2018-9-9',
                operator:'cc',
                corporate:'dd',
                setUpTime:'2015-09-02',
                storeAccount:10
                }, {
                    city: '',
                    name: '',
                    address: ' 996 ',
                    abbreviation:'',
                    corporateStart:'3000-8-8',
                    corporateEnd:'2080-9-9',
                    operator:'mm',
                    corporate:'qq',
                    setUpTime:'2015-09-02',
                    storeAccount:100
                }, {
                    city: '',
                    name: '',
                    address: ' 996 ',
                    abbreviation:'',
                    corporateStart:'3000-8-8',
                    corporateEnd:'2080-9-9',
                    operator:'mm',
                    corporate:'qq',
                    setUpTime:'2015-09-02',
                    storeAccount:100
                }, {
                    city: '',
                    name: '',
                    address: ' 996 ',
                    abbreviation:'',
                    corporateStart:'3000-8-8',
                    corporateEnd:'2080-9-9',
                    operator:'mm',
                    corporate:'qq',
                    setUpTime:'2015-09-02',
                    storeAccount:100
                }]
        }
    },
    created(){
        this.$http.get('/city/list')
            .then(function(data){
                console.log(data);
            })
            .catch(function(error){
            console.log(error)
            });
    },
    methods:{
        // ;
        addSuccess(addInfo){
            // ;

            //
            this.tableData.unshift(addInfo);
            this.pagination.currentPage=1;
            this.pagination.pageSize=10;
            this.pagination.total++;
        },
        // ;
        editSuccess(editInfo){
            // ;
            this.tableData.splice(this.companyInfoIndex,1,editInfo);
        },
        //form   
        formInfo(){

        },
        resetForm(formName) {
            this.$refs.form.resetFields();
        },
        //   
        reset(){
            this.form= {
                agent:'',//
                business:'',//
                city:'',//
                companyName:'',//
                timeStart:'',//
                timeEnd:'',//
                storeName: '',//
                storeAddress:'',//
                timeOver:''//
            }
        },
        //
        search(val){
            console.log(val,1111111)
            console.log(this.form);
        },
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
        },
        // 
        addCompany(){
            //  
            this.title='';
            this.isShow=true;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200); 
        },
        //
        editorCompany(index, row){
            console.log(row,'')
            //;
            this.companyInfoIndex=(this.pagination.currentPage-1)*this.pagination.pageSize+index;
            // ;
            this.currentCompanyInfo=row;
            this.title='';
            this.isShow=true;//;
            // ,setTimeout;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);  
        },
        // 
        bankAccount(index, row){
            this.$refs.commission.open();
        },
        //,
        handleEnd(index,row){
            this.noJoin='';
        },
        //,
        endJoin(){
            //;
        },
        //
        continueJoin(){
            this.noJoin='';
        }
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.pagination.currentPage-1)*this.pagination.pageSize,this.pagination.currentPage*this.pagination.pageSize);
        }
    }
}
</script>

<style>
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
    .el-dialog__footer{
        text-align: right;
    }
    .expire .el-form-item__content{
        margin-left: 0 !important;
    }
</style>





